var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("死亡射手")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Deadshot（弗洛伊德劳顿）是DC Comics出版的美国漫画书中出现的一个虚构角色。由David Vern Reed，Lew Schwartz和Bob Kane创作，这个角色首次出现在蝙蝠侠＃59（1950年6月至7月）。他是一名优秀的狙击手，经常吹嘘自己从未错过一次射门，并且通常被认为是DC宇宙中最致命的刺客之一。Deadshot是超级英雄蝙蝠侠的对手，属于组成他的盗贼画廊的敌人集体 。虽然通常被描绘成一个超级恶魔，但他有时被描绘成反英雄。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        Deadshot经常是一名雇佣的刺客，经常吹嘘“Never Miss”。他能够使用各种各样的武器，但最常被描绘为使用一对受压制的手腕式枪支。他最初作为一名新的犯罪斗士出现在高谭市，但当他试图取代黑暗骑士时，他被发现是蝙蝠侠的敌人。当蝙蝠侠和戈登专员将他送进监狱公开揭露他的阴谋成为哥谭的黑社会之王。服完他的任期后，Deadshot开始以刺客的身份招聘他的服装，将他的服装从他以前穿过的大礼帽和尾巴换成红色连身裤和独特的金属面板，右侧有一个瞄准装置。Deadshot的过去在随后的出场中被揭示出来。他的真名是弗洛伊德劳顿，他和他的母亲，虐待父亲和兄弟艾迪一起在一个富裕的家庭长大。埃迪深受他父母和其他市民的喜爱，而弗洛伊德是一个陷入困境的孩子，被父母鄙视，但弗洛伊德崇拜他的兄弟，并在与该镇的其他孩子发生冲突时一直站起来。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260"}})],1),_c('br'),_c('h2',[_vm._v("创作起源")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Deadshot 在其最新的两个化身中一直是自杀小队中的一个主要人物，他作为神射手的技能和他对人类生活的漠视有助于推进该组织的目标。因为他在自杀小队卷中没有他的面具而出现的爆头。5，＃20（2017年8月）。StjepanŠejić的艺术。\n他最明确的特征之一是渴望以壮观的方式死去，这是他加入自杀小队的主要动机。他觉得自己没有理由继续生活，虽然他不想自杀，但他根本不在乎他是否会死。引用了各种各样的理由，但最常见的是他父母对彼此的特殊仇恨。当面对一名威胁要将自杀小队暴露给世界的参议员时，Deadshot几乎要死了。他被命令阻止他的直接上级，里克旗，暗杀参议员，他自己杀死参议员，理由是他的命令是“阻止国旗杀死参议员。确切的言辞”。在此之后，警察在林肯纪念堂的台阶上枪杀了这个Deadshot 。他在伤口中幸存下来，继续留在队中。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180","height":"240","src":""}}),_c('v-card-title',{attrs:{"primary-title":""}},[_c('div',[_c('span')])])],1)],1),_c('